"use client";
import React, { useEffect, useState, useRef } from "react";

// Estructura de nodo de la lista doble
class Node {
  track: Track;
  prev: Node | null;
  next: Node | null;

  constructor(track: Track) {
    this.track = track;
    this.prev = null;
    this.next = null;
  }
}

// Lista doblemente enlazada para la playlist
class DoublyLinkedList {
  head: Node | null;
  tail: Node | null;

  constructor() {
    this.head = null;
    this.tail = null;
  }

  push(track: Track): Node {
    const newNode = new Node(track);
    if (!this.head) {
      this.head = this.tail = newNode;
    } else {
      if (this.tail) {
        this.tail.next = newNode;
        newNode.prev = this.tail;
        this.tail = newNode;
      }
    }
    return newNode; // devolvemos el nodo insertado
  }
}

// Tipo para las canciones
type Track = {
  id: string;
  name: string;
  artist_name: string;
  audio: string;
};

// Componente principal
export default function Home() {
  const [tracks, setTracks] = useState<Track[]>([]);
  const [playlist, setPlaylist] = useState<Track[]>([]);
  const [currentTrack, setCurrentTrack] = useState<Track | null>(null);
  const [isPlaying, setIsPlaying] = useState(false);
  const [search, setSearch] = useState("");

  const audioRef = useRef<HTMLAudioElement | null>(null);
  const playlistRef = useRef(new DoublyLinkedList());
  const currentNodeRef = useRef<Node | null>(null);

  const [, forceUpdate] = useState({});

  // Obtener canciones de Jamendo
  useEffect(() => {
    fetch(
      `https://api.jamendo.com/v3.0/tracks/?client_id=7f62a8e7&format=jsonpretty&limit=10&fuzzytags=rock`
    )
      .then((res) => res.json())
      .then((data) => setTracks(data.results));
  }, []);

  // Reproducir un nodo
  function playNode(node: Node) {
    if (!audioRef.current) return;

    currentNodeRef.current = node;
    setCurrentTrack(node.track);
    audioRef.current.src = node.track.audio;
    audioRef.current.play();
    setIsPlaying(true);
  }

  // A√±adir a playlist (y reproducir si autoPlay = true)
  function addToPlaylist(track: Track, autoPlay = false) {
    const node = playlistRef.current.push(track);
    setPlaylist([...playlist, track]); // actualizar UI
    forceUpdate({});

    if (autoPlay) {
      playNode(node);
    }

    return node;
  }

  // Controles de m√∫sica
  function playPause() {
    if (!audioRef.current) return;
    if (isPlaying) {
      audioRef.current.pause();
      setIsPlaying(false);
    } else {
      audioRef.current.play();
      setIsPlaying(true);
    }
  }

  function nextTrack() {
    if (currentNodeRef.current?.next) {
      playNode(currentNodeRef.current.next);
    }
  }

  function prevTrack() {
    if (currentNodeRef.current?.prev) {
      playNode(currentNodeRef.current.prev);
    }
  }

  // Filtrar canciones por b√∫squeda
  const filteredTracks = tracks.filter((t) =>
    t.name.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="p-6 space-y-6">
      <h1 className="text-xl font-bold">üé∂ Reproductor con Lista Doble</h1>

      {/* Barra de b√∫squeda */}
      <input
        type="text"
        placeholder="Buscar canci√≥n..."
        value={search}
        onChange={(e) => setSearch(e.target.value)}
        className="border p-2 rounded w-full"
      />

      {/* Resultados de b√∫squeda */}
      <div>
        <h2 className="text-lg font-semibold">Resultados:</h2>
        <ul className="space-y-2">
          {filteredTracks.map((track) => (
            <li
              key={track.id}
              className="flex justify-between items-center border p-2 rounded"
            >
              <span>
                {track.name} - {track.artist_name}
              </span>
              <div className="space-x-2">
                <button
                  onClick={() => addToPlaylist(track, true)}
                  className="bg-green-500 text-white px-3 py-1 rounded"
                >
                  ‚ñ∂Ô∏è Reproducir
                </button>
                <button
                  onClick={() => addToPlaylist(track)}
                  className="bg-blue-500 text-white px-3 py-1 rounded"
                >
                  ‚ûï A√±adir
                </button>
              </div>
            </li>
          ))}
        </ul>
      </div>

      {/* Playlist */}
      <div>
        <h2 className="text-lg font-semibold">Playlist:</h2>
        <ul className="space-y-2">
          {playlist.map((track, i) => (
            <li
              key={i}
              className={`p-2 rounded ${
                currentTrack?.id === track.id ? "bg-yellow-200" : "bg-gray-100"
              }`}
            >
              {track.name} - {track.artist_name}
            </li>
          ))}
        </ul>
      </div>

      {/* Controles */}
      <div className="flex space-x-4">
        <button
          onClick={prevTrack}
          className="bg-gray-300 px-3 py-1 rounded shadow"
        >
          ‚èÆ
        </button>
        <button
          onClick={playPause}
          className="bg-gray-300 px-3 py-1 rounded shadow"
        >
          {isPlaying ? "‚è∏ Pausa" : "‚ñ∂Ô∏è Play"}
        </button>
        <button
          onClick={nextTrack}
          className="bg-gray-300 px-3 py-1 rounded shadow"
        >
          ‚è≠
        </button>
      </div>

      {/* Reproductor oculto */}
      <audio ref={audioRef} onEnded={nextTrack} />
    </div>
  );
}
